import React, { useEffect, useState } from 'react'
import {
  useMediaQuery, useTheme, Grid, Box, Button, Table, TableContainer,
  TableHead, TableBody, TableRow, TableCell, Paper, Collapse
} from '@material-ui/core'
import {
  DateField,
  Filter,
  FilterProps,
  List,
  ListProps,
  Pagination,
  TextField,
  TextInput,
  useListContext,
  useListSortContext,
  SelectInput
} from 'react-admin'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown'
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux';
import { MobilePostPagination } from '../common/Pagination'
import { DateFilterInput } from './DateFilterInput';
import { ExtendedLog } from '../../core/logs/extended-log'
import { ResourceName } from '../../core/ResourceName';
import { getContrastColor } from '../../core/common/visual.utils';
import { reducerType } from '../../core/common/theme.reducer';

const DesktopRow = ({ data }: { data: ExtendedLog }) => {
  const [color, setColor] = useState(data.color ? data.color : "transparent")
  const history = useHistory()
  const textColor = useSelector((state: reducerType) => state.theme.darkMode === 'light' ? 'black' : 'white')

  useEffect(() => {
    if (data.color === null) {
      setColor("transparent")
    }
    else {
      setColor(data.color)
    }
  }, [data])

  return (
    <>
      <TableRow style={{ backgroundColor: color, height: "100%" }}>
        <TableCell>
          <DateField label="Data" record={data} source="date" showTime
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Urzdzenie" record={data} source="deviceName"
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Bilet" record={data} source="ticketCode"
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Opis" record={data} source="text" sortable={false}
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          {data.bookingId == null ?
            <div /> :
            <Button variant="contained" style={{ width: '100%', backgroundColor: 'rgb(220, 220, 220)' }}
              onClick={() => history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show` })}>
              {data.bookingId}</Button>}
        </TableCell>
      </TableRow>
    </>
  )
}

export const LogGrid = () => {
  const { ids, data } = useListContext()
  const { currentSort, setSort } = useListSortContext()

  const inverseOrder = (order: string) => (order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (sortBy: string) => {
    setSort(
      sortBy,
      sortBy === currentSort.field
        ? inverseOrder(currentSort.order)
        : 'DESC'
    )
  }

  const HeaderCell = (name: string, display: string) => (
    <>
      {currentSort.field === name
        ? <Button style={{ width: "100%", display: "flex", justifyContent: "flex-start" }}
          startIcon={currentSort.order === 'ASC' ? <ArrowUpwardIcon /> : <ArrowDownwardIcon />}>{display}</Button>
        : <Button style={{ width: "100%" }}>{display}</Button>}
    </>
  )

  return (
    <TableContainer component={Paper}>
      <Table aria-label="a dense table" size="small" >
        <TableHead>
          <TableRow>
            <TableCell onClick={() => handleSortChange("date")} style={{ width: "10%" }}>
              {HeaderCell("date", "Data")}</TableCell>
            <TableCell onClick={() => handleSortChange("deviceName")} style={{ width: "15%" }}>
              {HeaderCell("deviceName", "Urzdzenie")}</TableCell>
            <TableCell onClick={() => handleSortChange("ticketCode")} style={{ width: "25%" }}>
              {HeaderCell("ticketCode", "Bilet")}</TableCell>
            <TableCell style={{ textAlign: "center", width: "25%" }}>
              OPIS</TableCell>
            <TableCell onClick={() => handleSortChange("bookingId")} style={{ width: "15%" }}>
              {HeaderCell("bookingId", "Rezerwacja")}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <DesktopRow key={id} data={data[id] as ExtendedLog} />
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const MobileRow = ({ data }: { data: ExtendedLog }) => {
  const [open, setOpen] = useState(false)
  const [color, setColor] = useState("")
  const history = useHistory()
  const textColor = useSelector((state: reducerType) => state.theme.darkMode === 'light' ? 'black' : 'white')

  useEffect(() => {
    if (data.color === null) {
      setColor("transparent")
    }
    else {
      setColor(data.color)
    }
    setOpen(false)
  }, [data])

  return (
    <>
      <TableRow style={{ backgroundColor: color }}>
        <TableCell>
          <Button onClick={() => setOpen(!open)} style={{ width: "100%" }}>
            {open ?
              <ArrowDropUpIcon style={{ color: textColor }} /> :
              <ArrowDropDownIcon style={{ color: textColor }} />}
          </Button>
        </TableCell>
        <TableCell>
          <DateField label="Data" record={data} source="date" showTime
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Urzdzenie" record={data} source="deviceName"
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
        <TableCell>
          <TextField label="Bilet" record={data} source="ticketCode"
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }} />
        </TableCell>
      </TableRow>
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={4}>
          <Collapse in={open}>
            <Grid container style={{ marginTop: "3%" }}>
              <Grid item xs={6} style={{ display: "flex", justifyContent: "center", flexDirection: "column" }}>
                <Box>
                  Rezerwacja: </Box>
              </Grid>
              <Grid item xs={6} style={{ display: "flex", justifyContent: "flex-end" }}>
                {data.bookingId == null ?
                  <div /> :
                  <Button variant="contained" style={{ width: '50%', backgroundColor: 'rgb(220, 220, 220)' }}
                    onClick={() => history.push({ pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show` })}>
                    {data.bookingId}</Button>}
              </Grid>
              <hr style={{ width: "90%", marginTop: "3%", marginBottom: "3%" }} />
              <Grid item xs={6} style={{ display: "flex", justifyContent: "center", flexDirection: "column", marginBottom: "3%" }}>
                <div>Opis: </div>
              </Grid>
              <Grid item xs={6} style={{display: "flex", justifyContent: "center", flexDirection: "column", alignItems: "center", marginBottom: "3%"}}>
                {data.text}
              </Grid>
            </Grid>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  )
}

export const LogMobileGrid = () => {
  const { ids, data } = useListContext()
  const { currentSort, setSort } = useListSortContext()

  const inverseOrder = (order: string) => (order === 'ASC' ? 'DESC' : 'ASC')

  const handleSortChange = (sortBy: string) => {
    setSort(
      sortBy,
      sortBy === currentSort.field
        ? inverseOrder(currentSort.order)
        : 'DESC'
    )
  }

  const HeaderCell = (name: string, display: string) => (
    <>
      {currentSort.field === name
        ? <Button style={{ width: "100%" }}
          startIcon={currentSort.order === 'ASC' ? <ArrowUpwardIcon /> : <ArrowDownwardIcon />}>{display}</Button>
        : <Button style={{ width: "100%" }}>{display}</Button>}
    </>
  )

  return (
    <TableContainer component={Paper}>
      <Table aria-label="a dense table" size="small" >
        <TableHead>
          <TableRow>
            <TableCell size="small" style={{ width: "10%" }} />
            <TableCell onClick={() => handleSortChange("date")}>{HeaderCell("date", "Data")}</TableCell>
            <TableCell onClick={() => handleSortChange("deviceName")}>{HeaderCell("deviceName", "Urzdzenie")}</TableCell>
            <TableCell onClick={() => handleSortChange("ticketCode")}>{HeaderCell("ticketCode", "Bilet")}</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <MobileRow key={id} data={data[id] as ExtendedLog} />
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

const ListFilters = (props: Omit<FilterProps, 'children'>) => (
  <Filter {...props}>
    <TextInput label="Kod biletu" source="ticketCode" alwaysOn />
    <SelectInput label="Urzdzenie" source="deviceName" choices={[
      { id: 'Czytnik', name: 'Czytnik' },
      { id: 'Przej\u015Bcie', name: 'Przej\u015Bcie' }]} />
    <DateFilterInput source="date" key="date" />
    <TextInput label="Rezerwacja" source="bookingId" />
    <TextInput label="Wydarzenie" source="event" />
  </Filter>
)

export function LogList(props: ListProps) {
  const theme = useTheme()
  return (
    <>
      {useMediaQuery(theme.breakpoints.down('sm')) ?
        <List
          perPage={20}
          exporter={false}
          pagination={<MobilePostPagination />}
          filters={<ListFilters />}
          sort={{ field: 'date', order: 'DESC' }}
          {...props}>
          <LogMobileGrid />
        </List> :
        <List
          perPage={20}
          exporter={false}
          pagination={<Pagination rowsPerPageOptions={[20, 50, 100]} />}
          filters={<ListFilters />}
          sort={{ field: 'date', order: 'DESC' }}
          {...props}>
          <LogGrid />
        </List>
      }
    </>
  )
}
