import React, { useEffect, useState } from 'react'
import { Grid, Button, Table, TableContainer, IconButton,
   TableHead, TableBody, TableRow, TableCell, Paper, Collapse } from '@material-ui/core'
import {
  DateField,
  List,
  ListProps,
  TextField,
  useListContext
} from 'react-admin'
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown'
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import { useHistory } from 'react-router-dom'
import { useSelector } from 'react-redux';
import { ExtendedLog } from '../../core/logs/extended-log'
import { ResourceName } from '../../core/ResourceName';
import { getContrastColor } from '../../core/common/visual.utils';
import { reducerType } from '../../core/common/theme.reducer';

const DashRow = ({data}:{data: ExtendedLog}) => {
  const [open, setOpen] = useState(false)
  const [color, setColor] = useState("")
  const history = useHistory()
  const textColor = useSelector((state: reducerType) => state.theme.darkMode === 'light' ? 'black' : 'white') 

  useEffect(() => {
    if (data.color === null) {
      setColor("transparent")
    }
    else {
      setColor(data.color)
    }
    setOpen(false)
  }, [data])

  return(
    <>
      <TableRow style={{backgroundColor: color}}>
        <TableCell>
          <IconButton onClick={() => setOpen(!open)} size="small">
            {open ?
            <ArrowDropUpIcon style={{color: textColor}}/> :
            <ArrowDropDownIcon style={{color: textColor}}/>}
          </IconButton>
        </TableCell>
        <TableCell style={{textAlign: "center"}}>
          <DateField label="Data" record={data} source="date" showTime 
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }}/>
        </TableCell>
        <TableCell style={{textAlign: "center"}}>
          <TextField label="Urzdzenie" record={data} source="deviceName" 
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }}/>
        </TableCell>
        <TableCell style={{textAlign: "center"}}>
          <TextField label="Bilet" record={data} source="ticketCode" 
            style={{ color: data.color != null ? getContrastColor(data.color) : textColor, fontWeight: data.color !== null && getContrastColor(data.color) === 'white' || data.color === null && textColor === 'white' ? 'bold' : 'normal' }}/>
        </TableCell>
      </TableRow> 
      <TableRow>
        <TableCell style={{ paddingBottom: 0, paddingTop: 0 }} colSpan={4}>
          <Collapse in={open}>
            <Grid container style={{marginTop: "3%"}}>
              <Grid item xs={4} style={{display: "flex", justifyContent: "center", flexDirection: "column"}}>
                <div style={{marginLeft: "1%"}}>Rezerwacja: </div>
              </Grid>
              <Grid item xs={8} style={{display: "flex", justifyContent: "flex-end"}}>
                {data.bookingId == null ?
                <div /> :
                <Button variant="contained" style={{width: '50%',backgroundColor: 'rgb(220, 220, 220)'}}
                onClick={() => history.push({pathname: `/${ResourceName.TICKET_DETAILS}/${data.bookingId}/show`})}>
                  {data.bookingId}</Button>}
              </Grid>
              <hr style={{width: "90%", marginTop: "3%", marginBottom: "3%"}}/>
              <Grid item xs={4} style={{display: "flex", justifyContent: "center", flexDirection: "column", marginBottom: "3%"}}>
                <div style={{marginLeft: "1%"}}>Opis: </div>
              </Grid>
              <Grid item xs={8} style={{display: "flex", justifyContent: "center", flexDirection: "column", alignItems: "center", marginBottom: "3%"}}>
                {data.text}
              </Grid>
            </Grid>
          </Collapse>
        </TableCell>
      </TableRow>
    </>
  )
}

const LogGrid = () => {
  const { ids, data } = useListContext()

  return(
    <TableContainer component={Paper}>
      <Table aria-label="a dense table" size="small" padding="none" >
        <TableHead>
          <TableRow>
            <TableCell style={{width: "5%"}}/>
            <TableCell style={{width: "20%", textAlign: "center"}}>Data</TableCell>
            <TableCell style={{width: "30%", textAlign: "center"}}>Urzdzenie</TableCell>
            <TableCell style={{width: "45%", textAlign: "center"}}>Bilet</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {ids.map(id =>
            <DashRow key={id} data={data[id] as ExtendedLog}/>
          )}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export function LastLogList(props: ListProps) {

  return(
    <>
      <List
        {...props}
        perPage={9}
        exporter={false}
        pagination={false}
        actions={false}
        sort={{ field: 'date', order: 'DESC' }}>
          <LogGrid/>
      </List> 
    </>
  )
}
